export default{
	name:"star",
	data(){
		return{
			yellowScore:0,
			greyScore:5
		}
	},
	onLoad(){
		
	},
	props:{
		score:0,         //从父组件传入的分数
		showScore:0      //从父组件传入，是否显示分数 0不显示1是显示
	},
	created(){
		var yellowCont=0;
		if(this.score !=null && this.score !=undefined && this.score!=''){
			yellowCont=this.score;
		}
		var yell_n=parseInt(yellowCont/2);
		var grey_n=5-yell_n;
		
		this.yellowScore=yell_n;
		this.greyScore=grey_n;
		
	},
	methods:{
		
	},
}
